import React from 'react'
import {nanoid} from 'nanoid'
import { project } from './data-set' 


function Projects() {

    document.title = "Projects worked with"

    return (
        project.map((frame) => {
            return (
                <div className="main--card" key={nanoid()}>
                    <div className="card">
                        <div className="circles">
                            <div className="c"></div>
                            <div className="c"></div>
                            <div className="c"></div>
                        </div>
                        <div className="browser">

                            <div className="chevrons">
                                <svg viewBox="0 0 20 20" height="16" width="16" xmlns="http://www.w3.org/2000/svg" data-name="20" id="_20">
                                    <path transform="translate(6.25 3.75)" d="M0,6.25,6.25,0l.875.875L1.75,6.25l5.375,5.375L6.25,12.5Z" id="Fill"></path>
                                </svg>
                                &nbsp;&nbsp;
                                <svg viewBox="0 0 20 20" height="16" width="16" xmlns="http://www.w3.org/2000/svg" data-name="20" id="_20">
                                    <path transform="translate(6.625 3.75)" d="M7.125,6.25.875,12.5,0,11.625,5.375,6.25,0,.875.875,0Z" id="Fill"></path>
                                </svg>  
                            </div>

                            {/* sewarch bar */}
                            <div className="search-bar">
                                <center>https://{frame.name}/</center>
                            </div>
                        </div>
                    </div> 
                        <iframe src={frame.url} title={frame.name} ></iframe> 
                </div>
            )
        })
    )
}

export default Projects